import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Ponto_mapa } from '../../../modelo/Ponto_mapa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'ponto_mapa-dados-details',   templateUrl: 'ponto_mapa_dadosDetails.component.html' }) export class Ponto_mapaDadosDetailsComponent implements OnInit {    ponto_mapa: Ponto_mapa;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.ponto_mapa.Ponto_mapa";                 let id = +params['id'];         this.ponto_mapa = new Ponto_mapa();         this.ponto_mapa.cd_ponto_mapa = id;         o.objetoJson = this.ponto_mapa;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a ponto_mapa. '; }      this.ponto_mapa = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


